<template>
	<view class="content">
		<u-row>
			<u-col span="12" style="text-align: center;min-height: 104rpx;padding-top: 24rpx;">
				<u-search shape="round" v-model="keyword" @search="Tosearch" @custom="Tosearch"></u-search>
			</u-col>
		</u-row>
		<u-dropdown>
			<u-dropdown-item v-model="value1" title="A积分排序" :options="options1" @change="changeA"></u-dropdown-item>
			<u-dropdown-item v-model="value2" title="B积分排序" :options="options2" @change="changeB"></u-dropdown-item>
			<u-dropdown-item v-model="value3" title="时间选择" :options="options3" @change="changeT"></u-dropdown-item>
		</u-dropdown>
		<scroll-view scroll-y style="height: 80vh;">
			<view class="list02" v-for="(item,index) in zxlist" :key="index" @click="details(item.id)">
				<u-row>
					<u-col span="2">
						<u-avatar :src="item.userFile" size="90"></u-avatar>
					</u-col>
					<u-col span="4">
						<p class="titlebox">{{item.universalAntiUserMain27}}</p>
						<p class="pbox blue">{{item.orleFid}}</p>
					</u-col>
					<u-col span="6" style="text-align: right;">
						<p class="pbox">{{item.orgName}}</p>
						<p class="pbox">A：{{item.scoreA}} B：{{item.scoreB}}</p>
					</u-col>
				</u-row>
			</view>
		</scroll-view>
	</view>
</template>

<script>
	import {
		getLeader
	} from '../../api/organization.js'
	export default {
		data() {
			return {
				params: {
					pageNum: 1,
					pageSize: 3
				},
				zxlist: [],
				search: 1,
				keyword: '',
				show: 1,
				form: {},
				current: 0,
				swiperCurrent: 0,
				list: [{
					name: '反恐资讯'
				}, {
					name: '活动动态'
				}],
				list01: [{
					name: '可疑物品',
					time: '3天前',
					pic: '/static/img/banner.png'
				}, {
					name: '反恐防恐小常识：如何识别恐怖袭击嫌疑人？',
					time: '3天前',
					pic: '/static/img/banner.png'
				}, {
					name: '反恐',
					time: '3天前',
					pic: '/static/img/banner.png'
				}, ],
				value1: 1,
				value2: 2,
				value3: 3,
				options1: [{
						label: '升序',
						value: 'ASC',
					},
					{
						label: '降序',
						value: 'DESC',
					}
				],
				options2: [{
						label: '升序',
						value: 'ASC',
					},
					{
						label: '降序',
						value: 'DESC',
					}
				],
				options3: [],
			}
		},
		onLoad() {
			this.getNf();
			this.listF();
		},
		methods: {
			Tosearch() {
				if (this.keyword) {
					this.params.queryJson = [{
						data1: 'universalAntiUserMain27',
						data2: '2',
						data3: this.keyword,
						objectId: "294",
						subType: null,
						flag: false,
						fileType: "String"
					}];
					this.params.queryJson = JSON.stringify(this.params.queryJson);
					this.listF();
				}
			},
			listF(){
				getLeader(this.params).then(res => {
					if (res.code == 200) {
						this.zxlist = res.data;
					}
				})
			},
			changeA(v){
				this.params.sortA=v;
				this.listF();
			},
			changeB(v){
				this.params.sortB=v;
				this.listF();
			},
			changeT(v){
				this.params.sortDate=v;
				this.listF();
			},
			getNf() {
				var nfOptionsArray = new Array();
				var years = new Date().getFullYear();
				for (var i = years; i >= years -3; i--) {
					var anOption = {};
					anOption.value = i;
					anOption.label = i;
					nfOptionsArray.push(anOption);
				}
				this.options3 = nfOptionsArray;
			},
			details(row) {
				uni.navigateTo({
					url: 'details?id=' + row
				})
			},
		}
	}
</script>

<style scoped>
	.content {
		width: 100vw;
		height: 100vh;
		background: #F5F6FA;
	}

	.content /deep/ .grid-text {
		font-size: 24rpx;
		font-weight: bold;
		color: #333333;
		margin-top: 16rpx;
	}

	.blueline {
		position: absolute;
		left: 0;
		top: 24rpx;
		width: 6rpx;
		height: 28rpx;
		background: #0062E9;
	}

	.list02 {
		width: 100%;
		height: 130rpx;
		background: #FFFFFF;
		border-bottom: 1px solid #EEEEEE;
		position: relative;
		font-size: 24rpx;
		font-weight: 400;
		color: #333333;
		padding: 18rpx 50rpx 20rpx 40rpx;
	}

	.list02 .titlebox {
		font-size: 28rpx;
		font-weight: bold;
		color: #333333;
		width: 98%;
		/*宽度记得设置，这样超出宽度的内容才会被省略*/
		overflow: hidden;
		/*文本超出隐藏*/
		white-space: nowrap;
		text-overflow: ellipsis;
	}

	.list02 .pbox {
		margin-top: 8rpx;
		font-size: 24rpx;
		color: #999999;
		width: 98%;
		/*宽度记得设置，这样超出宽度的内容才会被省略*/
		overflow: hidden;
		/*文本超出隐藏*/
		white-space: nowrap;
		text-overflow: ellipsis;
		/*文本超出省略*/
	}

	.list02 .blue {
		color: #4490FB;
	}


	.list02 b {
		font-size: 24rpx;
		color: #296DCB;
		font-weight: normal;
	}

	.tiptxt {
		position: absolute;
		right: 32rpx;
		top: 20rpx;
	}

	.bz01 {
		width: 52rpx;
		vertical-align: middle;
		display: inline-block;
		margin-left: -10rpx;
	}

	.bz02 {
		width: 28rpx;
		margin-right: 6rpx;
		vertical-align: middle;
		display: inline-block;
	}
</style>